// "use client";
import { useEffect, useState } from "react";
import { API } from './API/page';
import type { IProducts, IProductsResponse } from './types/page';

const Home = () => {
  const [products, setProducts] = useState<IProducts[]>([]);
  const [loading, setLoading] = useState(true);
  const [error, setError] = useState<string | null>(null);

  useEffect(() => {
    const fetchProducts = async () => {
      try {
        setLoading(true);
        const res = await API.get<IProductsResponse>('/products');
        
        console.log('API javob:', res.data);
        
        // DummyJSON dan kelgan ma'lumot res.data.products da
        if (res.data && res.data.products) {
          setProducts(res.data.products);
          setError(null);
        } else {
          setError('Ma\'lumot topilmadi');
        }
      } catch (err) {
        console.error('Xatolik:', err);
        setError('Ma\'lumot yuklashda xatolik yuz berdi');
      } finally {
        setLoading(false);
      }
    };

    fetchProducts();
  }, []); // Faqat bir marta ishga tushadi

  if (loading) {
    return (
      <main style={{ padding: '20px', textAlign: 'center' }}>
        <div>Yuklanmoqda...</div>
      </main>
    );
  }

  if (error) {
    return (
      <main style={{ padding: '20px', textAlign: 'center' }}>
        <div style={{ color: 'red' }}>{error}</div>
      </main>
    );
  }

  return (
    <main style={{ padding: '20px' }}>
      <h1>Mahsulotlar</h1>
      <p>Jami: {products.length} ta mahsulot</p>
      
      <div style={{ 
        display: 'grid', 
        gridTemplateColumns: 'repeat(auto-fill, minmax(250px, 1fr))',
        gap: '20px',
        marginTop: '20px'
      }}>
        {products.map((item) => (
          <div 
            key={item.id} 
            style={{ 
              border: '1px solid #ddd', 
              borderRadius: '8px',
              padding: '15px',
              backgroundColor: '#fff'
            }}
          >
            <h3 style={{ margin: '10px 0' }}>{item.title}</h3>
            <p style={{ color: '#666', fontSize: '14px' }}>
              {item.description.substring(0, 100)}...
            </p>
            <div style={{ marginTop: '10px' }}>
              <p><strong>Narxi:</strong> ${item.price}</p>
              <p><strong>Chegirma:</strong> {item.discountPercentage}%</p>
              <p><strong>Reyting:</strong> ‚≠ê {item.rating}</p>
              <p><strong>Omborda:</strong> {item.stock} ta</p>
            </div>
          </div>
        ))}
      </div>
    </main>
  );
}

export default Home;